.Item-List-Cont-TopR {
  display: grid;
  grid-auto-flow: row;
  align-items: center;
  justify-content: center;
  font-size: 0.5vw;
  transition: 0.15s;
}

.Item-List-Cont-BottomR {
  flex: 1;
  display: grid;
  border-bottom: 0px;
  border-top: 4px;
  border-right: 0px;
  border-left: 0px;
  border-style: solid;
  border-color: var(--primary);
  margin-left: 15px;
  margin-right: 15px;
  font-size: 0.5vw;
  animation-name: animate-fade;
  animation-duration: 0.25s;
}
.ItemR {
  background-color: var(--secondary);
  transition: 0.15s;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  user-select: none;
  width: 95%;
  animation-name: animate-fade;
  animation-duration: 0.25s;
  color: var(--text);
  border-style: solid;
  border-color: var(--border);
}
.ItemR:hover {
  scale: 1.01;
  border-color: var(--primary);
}

.Begin {
  font-weight: bolder;
  font-size: 1vw;
}
@keyframes animate-fade {
  0% {
    opacity: 0;
    transform: scale(0.85, 0.85);
  }
  100% {
    opacity: 1;
    transform: scale(1, 1);
  }
}

@media only screen and (min-width: 600px) {
  .Item-List-Cont-TopR {
    grid-template-columns: 1.5fr 1fr 1.5fr 2fr 2fr 1.2fr;
    column-gap: 20px;
    margin-left: 15px;
    margin-right: 8px;
  }
  .mimg {
    width: 3vw;
  }
}

@media only screen and (max-width: 600px) {
  .Item-List-Cont-TopR {
    display: flex;
    flex-wrap: wrap;
    /* grid-template-columns: 0.5fr 2fr 1.5fr 2fr;
    grid-template-rows: 1fr 1fr; */
    column-gap: 2vw;
    margin-right: 8px;
    font-size: 1.3vw;
    margin-left: 3vw;
  }
  .Item-List-Cont-BottomR {
    font-size: 1.2vw;
  }

  .Begin {
    font-size: 3vw;
  }
  .mimg {
    width: 10vw;
  }
}
