.Container {
  display: grid;
  justify-content: center;
  justify-items: center;
  padding-bottom: 10vh;
}
.searchBoxBackground {
  margin-top: 30px;
  margin-bottom: 30px;
}
.SideBar {
  background-color: var(--background);
  color: var(--background);
}
.Page {
  display: flex;
  flex-direction: row;
  max-width: 100vw;
  padding-top: 10px;
  background-color: var(--background);
}
.Item {
  max-width: 85vw;
  background-color: var(--secondary);
}
.Center {
  flex: 1;
}

@media only screen and (min-width: 600px) {
  .Container {
    row-gap: 20px;
  }
  .searchBoxBackground {
    display: grid;
    align-content: center;
    justify-content: space-evenly;
    align-items: center;
    grid-template-columns: 25% 25% 25%;
  }
  .SideBar {
    max-width: 300px;
  }
}
@media only screen and (max-width: 600px) {
  .Container {
    row-gap: 20px;
  }
  .searchBoxBackground {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 50px;
    justify-items: center;
    align-items: start;
  }
  .SideBar {
    max-width: 100vw;
  }
}
